import UserQualification from "./userQualification-model.js";
const userQualification = new UserQualification();
export default {
	data() {
		return {
			userInfo: {},
			info: {}
		};
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.getDetails(() => {
				callBack && callBack();
			})
		},
		// 详情加载
		getDetails(callBack) {
			const that = this
			userQualification.getDetails({
				openid: that.userInfo.openid,
				id: that.userInfo.id
			}, (res) => {
				if (res.code == 4000) {
					that.info = res.data
					// userQualification.show_tips(res.explain)
				}
				callBack && callBack();
			})
		},
		goValidation() {
			const that = this
			let userInfo = that.userInfo
			// validation => 0  未认证
			// validation => 1  认证
			// validation => 2  审核中
			console.log('=======', userInfo)
			if (userInfo.validation == 0) {
				// 客户经理资质 未认证
				userQualification.navigate_to(`/pages/certification/certification`);
			} else if (userInfo.validation == 2) {
				// 客户经理资质 审核中
				userQualification.show_tips('资质审核中')
				return false
			}
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		const that = this
		that._onLoad()
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return userQualification.onShareAppMessage({});
	}
}
